import React, { memo, useMemo } from 'react';

import dynamic from 'next/dynamic';
import {
  I_HotspotConfigStyleShape,
  TypeActionInteraction,
} from 'containers/Home/types';
import { ShapeIconWrapper } from './styled';

const SvgContainer = dynamic(() => import('@components/SvgContainer'));

interface Props {
  size?: number;
  bgColorDefault: string;
  iconColorDefault: string;
  typeHotspot: TypeActionInteraction;
  hotspotStyleShape?: I_HotspotConfigStyleShape;
}

const ShapeIcon = ({
  size = 3,
  typeHotspot,
  bgColorDefault,
  iconColorDefault,
  hotspotStyleShape,
}: Props) => {
  const svgSize = 6 * size;
  const svg = hotspotStyleShape?.svg;
  const iconType = hotspotStyleShape?.icon_type || 'fontawesome';
  const shouldRenderSvg = iconType === 'svg' && !!svg;

  const generateShape = useMemo(() => {
    switch (hotspotStyleShape?.shape) {
      case 'star':
        return (
          <SvgContainer
            src={`${process.env.NEXT_PUBLIC_ASSETS_URL}/svg/Star.svg`}
          />
        );
      case 'diamond':
        return (
          <SvgContainer
            src={`${process.env.NEXT_PUBLIC_ASSETS_URL}/svg/Diamond.svg`}
          />
        );
      case 'certificate':
        return (
          <SvgContainer
            src={`${process.env.NEXT_PUBLIC_ASSETS_URL}/svg/Certificate.svg`}
          />
        );
      // case 'octagon':
      //   return (
      //     <SvgContainer
      //       src={`${process.env.NEXT_PUBLIC_ASSETS_URL}/svg/Octagon.svg`}
      //     />
      //   );
      case 'yinyang':
        return (
          <SvgContainer
            src={`${process.env.NEXT_PUBLIC_ASSETS_URL}/svg/YinYang.svg`}
          />
        );
      case 'hexagon':
        return (
          <SvgContainer
            src={`${process.env.NEXT_PUBLIC_ASSETS_URL}/svg/Hexagon.svg`}
          />
        );
      case 'heart':
        return (
          <SvgContainer
            src={`${process.env.NEXT_PUBLIC_ASSETS_URL}/svg/Heart.svg`}
          />
        );
      // case 'circle':
      //   return (
      //     <SvgContainer
      //       src={`${process.env.NEXT_PUBLIC_ASSETS_URL}/svg/Circle.svg`}
      //     />
      //   );
      case 'triangle':
        return (
          <SvgContainer
            src={`${process.env.NEXT_PUBLIC_ASSETS_URL}/svg/Triangle.svg`}
          />
        );
      case 'square':
        return (
          <SvgContainer
            src={`${process.env.NEXT_PUBLIC_ASSETS_URL}/svg/Square.svg`}
          />
        );
      default:
        return '';
    }
  }, [hotspotStyleShape]);

  const classIcon = useMemo(() => {
    const cls = hotspotStyleShape?.icon?.split('/')[1];
    if (cls) return cls;
    switch (typeHotspot) {
      case 'point':
        return 'fas fa-map-marker-alt';
      case 'article':
        return 'fas fa-newspaper';
      case 'link':
        return 'fas fa-link';
      case 'sound':
        return 'fas fa-waveform';
      case 'video':
        return 'fas fa-video';
      case 'image':
        return 'fas fa-images';
      default:
        return '';
    }
  }, [hotspotStyleShape, typeHotspot]);

  return (
    <ShapeIconWrapper
      iconColor={iconColorDefault}
      isRenderShape={!!hotspotStyleShape?.shape}
      className={hotspotStyleShape?.shape || ''}
      bgColor={hotspotStyleShape?.bg_color || bgColorDefault}
    >
      <div className="--svg">{generateShape}</div>
      {shouldRenderSvg ? (
        <span
          className="icon"
          style={{ width: `${svgSize}px`, height: `${svgSize}px` }}
          dangerouslySetInnerHTML={{ __html: svg }}
        />
      ) : (
        <span className="icon">
          <i aria-hidden className={classIcon} />
        </span>
      )}
    </ShapeIconWrapper>
  );
};

export default memo(ShapeIcon);
